@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap')s;

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}



.body::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('/public/eucalyptus.jpg');
    background-size: cover;
    background-position: center;
    filter: brightness(70%);
    z-index: -1;
}

#download {
    display: block;
    margin: 0 auto;
    margin-top: 15px;
    width: 20%;
    cursor: pointer;
}

.head {
    display: flex;
    justify-content: flex-end;
    cursor: pointer;
}

nav {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 3rem;
    background-color: rgba(50, 50, 50, 0.2);
    backdrop-filter: blur(3px);
    z-index: 1000;
}

.nav-logo {
    letter-spacing: 1px;
    font-size: 20px;
    font-weight: bold;
    padding-left: 20px;
    padding-top: 10px;
    color: white;
}

.button {
    color: #6c8478;
    font-size: 60px;
    height: 65px;
    position: fixed;
    right: 0;
    bottom: 0;
    margin: 2rem;
    cursor: pointer;
}

.container {
    background-color: rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(5px);
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 60%;
    height: 80%;
    border-radius: 6px;
    box-shadow: 0 0px 10px rgb(0, 0, 0, 0.6);
    padding: 1%;
    padding-bottom: 2%;
}

.close {
    height: 10px;
    width: 10px;
}

.delete {
    display: block;
    margin: 0 auto;
    margin-top: 15px;
    width: 60%;
    cursor: pointer;
    border-radius: 8%;
    border: 1px solid white;
    background-color: #6c8478;
    color: white;
    height: 8%;
}

.grid {
    margin-top: 5%;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    justify-content: center;
    gap: 1rem;
    padding: 2%;
    background-color: rgb(200, 200, 200);
    width: 100%;
    height: 100vh;
}

.grid_item {
    height: 17rem;
    border-radius: 6px;
    overflow: hidden;
    box-shadow: 0 0px 10px rgba(0, 0, 0, 0.25);
}

.grid_item img,
.grid_item video {
    width: 100%;
    height: 100%;
    background-color: black;
}

.intro {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #202D10;
    background-color: rgba(0, 0, 0, 0.7);
    color: white;
    border-radius: 1%;
    font-family: 'Cedarville cursive', cursive;
}

.intro h1,
.intro button {
    margin: 0 auto;
}

.intro h1 {
    font-family: "Petit formal script", cursive;
}

.intro button {
    display: inline;
    width: 10%;
    height: 2rem;
    margin-top: 5%;
    cursor: pointer;
    background-color: #6c8478;
    color: white;
    border: 2px solid white;
    font-weight: bold;
}

video {
    cursor: pointer;
}

h1 {
    font-size: 2.4rem;
    font-weight: 600;
    width: auto;
}

.heading {
    text-align: center;
    padding-top: 5%;
    font-weight: bold;
    color: white;
}

.name {
    font-size: 4.5rem;
    color: white;
}

.preview {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    justify-content: center;
    gap: 1rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
    overflow: auto;
    height: 80%;
}

.preview_item {
    min-height: 10rem;
    width: 100%;
    border-radius: 6px;
    overflow: hidden;
    margin: 0 auto;
    height: 6rem;
    object-fit: cover;
}

.preview_item img,
.preview_item video {
    height: 100%;
    width: 100%;
}

.upload {
    display: block;
    margin: 0 auto;
    width: 50%;
    height: 10%;
    border-radius: 10px;
    color: white;
    background-color: #6c8478;
    border: 2px solid white;
    font-size: 150%;
    cursor: pointer;
}

@media screen and (max-width: 768px) {
    .grid {
        grid-template-columns: 1fr;
    }
}

@media screen and (max-width: 768px) {
    nav {
        display: none;
        /* Hide the navigation bar on screens smaller than 768px */
    }

    .intro-box {
        height: 90%;
        width: 90%;
        overflow-y: auto;
    }

    .name img {
        height: 100%;
        width: 13%;
    }
}